import { ComponentProps } from 'preact';
import { classNames } from '../utils/UNSAFE_classNames';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { CircleWrapper, CircleInner } from '../PRIVATE_Meter';
import { getClipPath } from '../utils/PRIVATE_meterUtils';
import { useTranslationBundle } from '../hooks/UNSAFE_useTranslationBundle';
import type { BundleType } from '../resources/nls/bundle';
import { HTMLAttributesSignalExcluded } from '../utils/UNSAFE_attributeUtils';

type ariaPropsType = ComponentProps<typeof CircleWrapper>['ariaProps'];
type IntrinsicProps = Pick<
  HTMLAttributesSignalExcluded<HTMLDivElement>,
  'id' | 'class' | 'children'
>;

type Props = IntrinsicProps & {
  /**
   * Specifies the id of the progress circle.  If the progress circle is being used to describe the loading process of a particular region
   * of the page, then the aria-describedby attribute of the region must point to the id of the ProgressCircle
   */
  id?: IntrinsicProps['id'];
  /**
   * The value of the progress circle.  If the value is 'indeterminate', an indeterminate progress circle will be shown.
   */
  value?: 'indeterminate' | number;

  /**
   * The maximum value of the progress circle.
   */
  max?: number;
  /**
   * The size of the progress circle.
   */
  size?: 'sm' | 'md' | 'lg';

  /**
   * A label to be used for accessibility purposes when value is set to 'indeterminate'.
   */
  accessibleLabel?: string;
};
/**
 * A progress circle allows the user to visualize the progression of an extended computer operation.
 */
export function ProgressCircle({ value, max, ...otherProps }: Props) {
  return value === 'indeterminate' ? (
    <IndeterminateProgressCircle {...otherProps}></IndeterminateProgressCircle>
  ) : (
    <DeterminateProgressCircle value={value} max={max} {...otherProps}></DeterminateProgressCircle>
  );
}

type IndeterminateProps = Omit<Props, 'value' | 'max'>;

const IndeterminateProgressCircle = ({ accessibleLabel, id, size = 'md' }: IndeterminateProps) => {
  const baseClasses = classNames([
    styles.base,
    `oj-c-progress-circle-${size}`,
    styles.indeterminateOuter
  ]);
  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');
  const ariaProps: ariaPropsType = {
    'aria-valuetext': accessibleLabel || translations.progressIndeterminate(),
    role: 'progressbar'
  };
  return (
    <CircleWrapper id={id} ariaProps={ariaProps} class={baseClasses}>
      <CircleInner class={styles.indeterminateInner} />
    </CircleWrapper>
  );
};

type DeterminateProps = Omit<Props, 'accessibleLabel'> & {
  value?: number;
};

const DeterminateProgressCircle = ({ value = 0, max = 100, id, size = 'md' }: DeterminateProps) => {
  const percentage = Math.min(Math.max(0, value / max), 1);
  const baseClasses = classNames([styles.base, `oj-c-progress-circle-${size}`]);
  const clipPath = getClipPath(90, percentage * 360, false, 'full');

  const ariaProps: ariaPropsType = {};
  ariaProps['aria-valuemin'] = value != null ? '0' : undefined;
  ariaProps['aria-valuemax'] = value != null ? `${max}` : undefined;
  ariaProps['aria-valuenow'] = value != null ? `${value}` : undefined;
  ariaProps['role'] = 'progressbar';
  return (
    <CircleWrapper id={id} ariaProps={ariaProps} class={baseClasses}>
      <CircleInner class={classNames([styles.determinateInner, styles.track])} />
      <CircleInner
        class={classNames([styles.determinateInner, styles.value])}
        clipPath={clipPath}
      />
    </CircleWrapper>
  );
};

const styles = {
  base: cssProps`${{
    position: 'relative',
    overflowX: 'hidden',
    overflowY: 'hidden',
    display: 'inline-block',
    maxWidth: '100%',
    maxHeight: '100%',
    width: 'var(--oj-c-PRIVATE-DO-NOT-USE-progress-circle-size)',
    height: 'var(--oj-c-PRIVATE-DO-NOT-USE-progress-circle-size)'
  }}`,
  indeterminateOuter: cssProps`${{
    animationName: 'var(--oj-c-PRIVATE-DO-NOT-USE-private-progress-circle-rotation-animation)',
    animationDuration: '1s',
    animationTimingFunction: 'linear',
    animationIterationCount: 'infinite'
  }}`,
  indeterminateInner: cssProps`${{
    borderWidth: 'var(--oj-c-PRIVATE-DO-NOT-USE-progress-circle-track-width)',
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-meter-value-bg-color)',
    borderStyle: 'solid',
    width: '100%',
    height: '100%',
    boxSizing: 'border-box',
    borderRadius: '50%',
    animationName: 'var(--oj-c-PRIVATE-DO-NOT-USE-private-progress-circle-length-animation)',
    animationDuration: '1.5s',
    animationTimingFunction: 'linear',
    animationIterationCount: 'infinite',
    animationDirection: 'alternate',
    position: 'absolute'
  }}`,
  determinateInner: cssProps`${{
    borderWidth: 'var(--oj-c-PRIVATE-DO-NOT-USE-progress-circle-track-width)',
    width: '100%',
    height: '100%',
    boxSizing: 'border-box',
    borderRadius: '50%',
    position: 'absolute',
    borderStyle: 'solid'
  }}`,
  track: cssProps`${{
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-meter-track-bg-color)'
  }}`,
  value: cssProps`${{
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-meter-value-bg-color)'
  }}`
};
